/**
 * SourcesModal Component
 * Displays article sources and quality assessment in a modal dialog
 */

class SourcesModal extends BaseModal {
  constructor(options = {}) {
    super('sources-modal', {
      title: 'Article Sources',
      icon: 'üîó',
      maxWidth: 'max-w-4xl',
      ...options,
    });

    this.article = null;
  }

  // Public API methods
  showSources(article) {
    this.article = article;

    if (this.hasSources(article)) {
      this.setContent(this.getSourcesContent(article));
    } else {
      this.setContent(this.getNoSourcesContent());
    }

    this.open();
  }

  hasSources(article) {
    return (
      article &&
      (article.primary_source ||
        article.sources ||
        article.annotated_source_list)
    );
  }

  getSourcesContent(article) {
    let content = '<div class="space-y-6">';

    // Primary Source
    if (article.primary_source) {
      content += this.getPrimarySourceSection(article.primary_source);
    }

    // Additional Sources
    if (article.sources) {
      content += this.getAdditionalSourcesSection(article.sources);
    }

    // Annotated Source List (Quality Assessment)
    if (article.annotated_source_list) {
      content += this.getQualityAssessmentSection(
        article.annotated_source_list
      );
    }

    content += '</div>';
    return content;
  }

  getPrimarySourceSection(primarySource) {
    return `
      <div class="bg-blue-50 dark:bg-blue-900/20 p-4 rounded-lg">
        <h3 class="text-lg font-semibold text-blue-800 dark:text-blue-200 mb-2 flex items-center">
          <span class="mr-2">üéØ</span>
          Primary Source
        </h3>
        <div class="prose dark:prose-invert max-w-none">
          <p class="text-gray-700 dark:text-gray-300">${this.formatSourceText(
            primarySource
          )}</p>
        </div>
      </div>
    `;
  }

  getAdditionalSourcesSection(sources) {
    return `
      <div class="bg-gray-50 dark:bg-gray-800/50 p-4 rounded-lg">
        <h3 class="text-lg font-semibold text-gray-800 dark:text-gray-200 mb-2 flex items-center">
          <span class="mr-2">üìö</span>
          Additional Sources
        </h3>
        <div class="prose dark:prose-invert max-w-none">
          <div class="text-gray-700 dark:text-gray-300">${this.formatSourceText(
            sources
          )}</div>
        </div>
      </div>
    `;
  }

  getQualityAssessmentSection(annotatedList) {
    return `
      <div class="bg-green-50 dark:bg-green-900/20 p-4 rounded-lg">
        <h3 class="text-lg font-semibold text-green-800 dark:text-green-200 mb-2 flex items-center">
          <span class="mr-2">‚úÖ</span>
          Source Quality Assessment
        </h3>
        <div class="prose dark:prose-invert max-w-none">
          <pre class="whitespace-pre-wrap text-sm bg-white dark:bg-gray-800 p-3 rounded border overflow-x-auto">${this.escapeHtml(
            annotatedList
          )}</pre>
        </div>
      </div>
    `;
  }

  getNoSourcesContent() {
    return `
      <div class="text-center text-gray-500">
        <div class="text-4xl mb-4">üîó</div>
        <p class="text-lg">No sources information available for this article.</p>
        <p class="text-sm mt-2 text-gray-400">Source information may be added in future updates.</p>
      </div>
    `;
  }

  formatSourceText(text) {
    // Format source text with basic markdown-like formatting
    return text
      .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
      .replace(/\*(.*?)\*/g, '<em>$1</em>')
      .replace(/\n\n/g, '</p><p>')
      .replace(/\n/g, '<br>')
      .replace(/^<p>/, '')
      .replace(/<\/p>$/, '');
  }

  escapeHtml(text) {
    const div = document.createElement('div');
    div.textContent = text;
    return div.innerHTML;
  }

  // Method to extract and format URLs from text
  linkifyUrls(text) {
    const urlRegex = /(https?:\/\/[^\s]+)/g;
    return text.replace(
      urlRegex,
      '<a href="$1" target="_blank" rel="noopener noreferrer" class="text-blue-600 hover:text-blue-800 underline">$1</a>'
    );
  }

  // Static method for easy usage
  static show(article) {
    if (!window.sourcesModalInstance) {
      window.sourcesModalInstance = new SourcesModal();
    }
    window.sourcesModalInstance.showSources(article);
  }
}

// Export for use in other modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = SourcesModal;
} else {
  window.SourcesModal = SourcesModal;
}
