/**
 * SummaryModal Component
 * Displays article summary in a modal dialog
 */

class SummaryModal extends BaseModal {
  constructor(options = {}) {
    super('summary-modal', {
      title: 'Article Summary',
      icon: 'üìÑ',
      maxWidth: 'max-w-2xl',
      ...options,
    });

    this.article = null;
  }

  // Public API methods
  showSummary(article) {
    this.article = article;

    if (article && article.summary) {
      this.setContent(this.getSummaryContent(article.summary));
    } else {
      this.setContent(this.getNoSummaryContent());
    }

    this.open();
  }

  getSummaryContent(summary) {
    return `
      <div class="prose dark:prose-invert max-w-none">
        <p class="text-lg leading-relaxed">${this.formatSummary(summary)}</p>
      </div>
    `;
  }

  getNoSummaryContent() {
    return `
      <div class="text-center text-gray-500">
        <div class="text-4xl mb-4">üìÑ</div>
        <p class="text-lg">No summary available for this article.</p>
        <p class="text-sm mt-2 text-gray-400">The article content is available in the main view.</p>
      </div>
    `;
  }

  formatSummary(summary) {
    // Basic formatting for summary text
    return summary
      .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
      .replace(/\*(.*?)\*/g, '<em>$1</em>')
      .replace(/\n\n/g, '</p><p class="text-lg leading-relaxed">')
      .replace(/\n/g, '<br>');
  }

  // Static method for easy usage
  static show(article) {
    if (!window.summaryModalInstance) {
      window.summaryModalInstance = new SummaryModal();
    }
    window.summaryModalInstance.showSummary(article);
  }
}

// Export for use in other modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = SummaryModal;
} else {
  window.SummaryModal = SummaryModal;
}
