// CSS is handled by the build process
const ArticleViewer = require('../components/article/ArticleViewer.js');
const SummaryModal = require('../components/modals/SummaryModal.js');
const SourcesModal = require('../components/modals/SourcesModal.js');
const ErrorHandler = require('../services/error/ErrorHandler.js');

class ArticlePage {
  constructor() {
    this.init();
  }

  init() {
    try {
      const articleContainer = document.querySelector('.article-container');
      if (articleContainer) {
        this.articleViewer = new ArticleViewer(articleContainer);
      }

      this.initializeModals();
      this.setupEventListeners();
      console.log('Article page initialized successfully');
    } catch (error) {
      ErrorHandler.handle(error, { context: 'Article page initialization' });
    }
  }

  initializeModals() {
    const summaryTriggers = document.querySelectorAll('[data-summary-modal]');
    summaryTriggers.forEach((trigger) => {
      new SummaryModal(trigger);
    });

    const sourcesTriggers = document.querySelectorAll('[data-sources-modal]');
    sourcesTriggers.forEach((trigger) => {
      new SourcesModal(trigger);
    });
  }

  setupEventListeners() {
    const shareButtons = document.querySelectorAll('.share-button');
    shareButtons.forEach((button) => {
      button.addEventListener('click', this.handleShare.bind(this));
    });

    const printButton = document.querySelector('.print-button');
    if (printButton) {
      printButton.addEventListener('click', () => window.print());
    }
  }

  handleShare(event) {
    const button = event.currentTarget;
    const platform = button.dataset.platform;
    const url = encodeURIComponent(window.location.href);
    const title = encodeURIComponent(document.title);
    let shareUrl = '';

    switch (platform) {
      case 'twitter':
        shareUrl = `https://twitter.com/intent/tweet?url=${url}&text=${title}`;
        break;
      case 'facebook':
        shareUrl = `https://www.facebook.com/sharer/sharer.php?u=${url}`;
        break;
      case 'linkedin':
        shareUrl = `https://www.linkedin.com/sharing/share-offsite/?url=${url}`;
        break;
    }

    if (shareUrl) {
      window.open(shareUrl, '_blank', 'width=600,height=400');
    }
  }
}

if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => new ArticlePage());
} else {
  new ArticlePage();
}

// Export for both CommonJS and ES modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = ArticlePage;
} else if (typeof window !== 'undefined') {
  window.ArticlePage = ArticlePage;
}
