/**
 * ArticleViewer Component
 * Handles the display and interaction of individual articles
 */

class ArticleViewer {
  constructor(container, options = {}) {
    this.container = container;
    this.options = {
      enableSummary: true,
      enableSources: true,
      enableSharing: true,
      ...options,
    };

    this.init();
  }

  init() {
    this.setupEventListeners();
    this.initializeFeatures();
  }

  setupEventListeners() {
    // Summary modal trigger
    const summaryBtn = this.container.querySelector('.summary-btn');
    if (summaryBtn) {
      summaryBtn.addEventListener('click', this.showSummary.bind(this));
    }

    // Sources modal trigger
    const sourcesBtn = this.container.querySelector('.sources-btn');
    if (sourcesBtn) {
      sourcesBtn.addEventListener('click', this.showSources.bind(this));
    }

    // Share functionality
    const shareBtn = this.container.querySelector('.share-btn');
    if (shareBtn) {
      shareBtn.addEventListener('click', this.handleShare.bind(this));
    }
  }

  initializeFeatures() {
    // Initialize reading progress
    this.initReadingProgress();

    // Initialize text-to-speech if available
    this.initTextToSpeech();
  }

  showSummary() {
    const articleData = this.getArticleData();
    if (articleData && articleData.summary) {
      // This will be handled by SummaryModal
      const event = new CustomEvent('showSummary', {
        detail: { summary: articleData.summary },
      });
      document.dispatchEvent(event);
    }
  }

  showSources() {
    const articleData = this.getArticleData();
    if (articleData && articleData.sources) {
      // This will be handled by SourcesModal
      const event = new CustomEvent('showSources', {
        detail: { sources: articleData.sources },
      });
      document.dispatchEvent(event);
    }
  }

  handleShare() {
    const articleData = this.getArticleData();
    if (navigator.share && articleData) {
      navigator
        .share({
          title: articleData.title,
          text: articleData.summary || 'Check out this article',
          url: window.location.href,
        })
        .catch(console.error);
    } else {
      // Fallback to clipboard
      this.copyToClipboard(window.location.href);
    }
  }

  copyToClipboard(text) {
    if (navigator.clipboard) {
      navigator.clipboard
        .writeText(text)
        .then(() => {
          this.showNotification('Link copied to clipboard!');
        })
        .catch(console.error);
    }
  }

  showNotification(message) {
    // Simple notification system
    const notification = document.createElement('div');
    notification.className = 'notification';
    notification.textContent = message;
    notification.style.cssText = `
      position: fixed;
      top: 20px;
      right: 20px;
      background: #4CAF50;
      color: white;
      padding: 12px 24px;
      border-radius: 4px;
      z-index: 1000;
      transition: opacity 0.3s;
    `;

    document.body.appendChild(notification);

    setTimeout(() => {
      notification.style.opacity = '0';
      setTimeout(() => {
        document.body.removeChild(notification);
      }, 300);
    }, 3000);
  }

  initReadingProgress() {
    const progressBar = document.createElement('div');
    progressBar.className = 'reading-progress';
    progressBar.style.cssText = `
      position: fixed;
      top: 0;
      left: 0;
      width: 0%;
      height: 3px;
      background: #007cba;
      z-index: 1000;
      transition: width 0.1s;
    `;

    document.body.appendChild(progressBar);

    window.addEventListener('scroll', () => {
      const winScroll =
        document.body.scrollTop || document.documentElement.scrollTop;
      const height =
        document.documentElement.scrollHeight -
        document.documentElement.clientHeight;
      const scrolled = (winScroll / height) * 100;
      progressBar.style.width = scrolled + '%';
    });
  }

  initTextToSpeech() {
    if ('speechSynthesis' in window) {
      const speakBtn = this.container.querySelector('.speak-btn');
      if (speakBtn) {
        speakBtn.addEventListener('click', this.toggleSpeech.bind(this));
      }
    }
  }

  toggleSpeech() {
    if (speechSynthesis.speaking) {
      speechSynthesis.cancel();
      return;
    }

    const articleText = this.container.querySelector('.article-content');
    if (articleText) {
      const utterance = new SpeechSynthesisUtterance(articleText.textContent);
      utterance.rate = 0.8;
      utterance.pitch = 1;
      speechSynthesis.speak(utterance);
    }
  }

  getArticleData() {
    // Try to get article data from various sources
    const dataElement = this.container.querySelector('[data-article]');
    if (dataElement) {
      try {
        return JSON.parse(dataElement.dataset.article);
      } catch (e) {
        console.warn('Failed to parse article data:', e);
      }
    }

    // Fallback to extracting from DOM
    return {
      title: this.container.querySelector('h1')?.textContent || '',
      summary:
        this.container.querySelector('.article-summary')?.textContent || '',
      content:
        this.container.querySelector('.article-content')?.textContent || '',
      sources: this.extractSources(),
    };
  }

  extractSources() {
    const sourceElements = this.container.querySelectorAll(
      '.source-link, [data-source]'
    );
    return Array.from(sourceElements).map((el) => ({
      title: el.textContent || el.dataset.title || 'Source',
      url: el.href || el.dataset.url || '#',
    }));
  }
}

// Export for use in other modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = ArticleViewer;
} else {
  window.ArticleViewer = ArticleViewer;
}
